.container {
  width: calc(100% - 60px);
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.container.medium-- {
  max-width: 960px;
}

.container.small-- {
  max-width: 760px;
}

.f {
  margin-top: 85px;
  margin-bottom: 85px;
}

.header {
  padding-top: 20px;
  padding-bottom: 20px;
  border-top: solid 1px #969696;
  border-bottom: solid 1px #969696;
}

.header__ctn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

@media screen and (max-width: 1199px) {
  .header__ctn {
    display: block;
  }
}

.header__ctn-left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

@media screen and (max-width: 767px) {
  .header__ctn-left {
    display: block;
  }
}

.header__ctn-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-left: -16px;
  margin-right: -16px;
}

@media screen and (max-width: 1199px) {
  .header__ctn-right {
    margin-top: 30px;
    justify-content: flex-start;
  }
}

@media screen and (max-width: 767px) {
  .header__ctn-right {
    margin-top: 0;
    flex-wrap: wrap;
  }
}

.header__ctn-right > * {
  margin-left: 16px;
  margin-right: 16px;
}

@media screen and (max-width: 767px) {
  .header__ctn-right > * {
    margin-top: 18px;
  }
}

.header__main-logo img {
  max-height: 45px;
  width: auto;
}

.header__nav {
  margin-left: 30px;
  max-width: 350px;
}

@media screen and (max-width: 767px) {
  .header__nav {
    max-width: 100%;
    margin-left: 0;
    margin-top: 18px;
  }
}

.header__nav > div > ul {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.header__nav > div > ul li {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

.header__nav > div > ul li.current_page_item a {
  text-decoration-color: #000;
}

.header__nav > div > ul li:not(:last-child):after {
  display: inline-block;
  content: '.';
  position: relative;
  top: -2px;
}

.header__nav > div > ul li a {
  display: block;
  padding: 10px;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  color: #000;
  text-decoration: underline;
  text-decoration-color: transparent;
  text-decoration-thickness: 2px;
  text-underline-position: under;
  transition: text-decoration-color .4s ease-in-out;
}

@media screen and (min-width: 1025px) {
  .header__nav > div > ul li a:hover {
    text-decoration-color: #000;
  }
}

.header__search input, .header__accessibility, .header__languages {
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: 500;
}

.header__search {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

.header__search input {
  display: block;
  margin-right: 10px;
  padding: 5px 18px;
  border-radius: 19px;
  background-color: #efefef;
}

.header__search button {
  display: block;
}

@media screen and (min-width: 1025px) {
  .header__search button {
    transition: opacity .4s ease-in-out;
  }
  .header__search button:hover {
    opacity: .5;
  }
}

.header__accessibility {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}

.header__accessibility-radio {
  position: relative;
  width: 39px;
  height: 21px;
  margin-left: 10px;
}

.header__accessibility-radio input {
  cursor: pointer;
  z-index: 2;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
